import React from 'react';
import 'antd/dist/antd.css';
import { Table, Input, Button, Icon, Modal, Divider, Popconfirm } from 'antd';
import Highlighter from 'react-highlight-words';
import WrappedRegistrationForm from './components/RegistrationForm';
import api from '../Global/Api';

class branch extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      searchedColumn: '',
      city:[],
      branch:[],
      branchdetail:{},
      isEditProduct: false,
    };
  }

componentDidMount() {
  this.fetchbranch();
  this.fetchcity();
}


  fetchbranch = (params = {}) => {
    api.get(`/api/branch`,{
       params: {
            ...params,
          },
          type: 'json',
      }).then(res => {
        this.setState({
          branch: res.data,
        });
      });
  };

  fetchcity = (params = {}) => {
    api.get(`/api/city`,{
       params: {
            ...params,
          },
          type: 'json',
      }).then(res => {
        this.setState({
          city: res.data,
        });
      });
  };

  onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleChange= async (id)=>{
    this.setState({ loading:true});
    await api.get(`/api/branch/${id}`, {})
    .then(res => {
      this.setState({
        branchdetail: res.data,
        visible: true,
        loading:false,
        isEditProduct:true
      });
    });
   };
    

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,isEditProduct:false,
    });
  };

  handleCancel = e => {
    this.setState({
      visible: false,isEditProduct:false,
    });
  };

  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text =>
      this.state.searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ) : (
        text
      ),
  });

  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex,
    });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };

  handleDelete= async (id)=>{
    this.setState({ loading:true});
    await api.delete(`/api/branch/${id}`, {});
    this.fetchbranch();
    this.setState({loading:false});
   };

  

  render() {

    const columns = [{
      dataIndex: 'id',
      title: 'Branch Id',
      key:'Id',
      defaultSortOrder: 'ascend',
      sorter: (a, b) => a.id - b.id,
      },
      {
        dataIndex: 'branch_name',
        title: 'Branch Name',
        key: 'branch_name',
        ...this.getColumnSearchProps('branch_name'),
      },
      {
        dataIndex: 'city.city_name',
        title: 'City Name',
        key: 'city_name'
      },
      {
        dataIndex: 'branch_status',
        title: 'Branch Status',
        key: 'branch_status' ,
        render: val => (val ? 'True' : 'False')
      },
      {
        dataIndex: 'id',
        title: 'Action',
        key: 'action',
        render: (text,record) => (
          <span>
          <Popconfirm title="Sure to edit?" onConfirm={() => this.handleChange(record.id)}>
          <a>Edit</a>
         </Popconfirm>
            <Divider type="vertical"/>
            <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.id)}>
            <a>Delete</a>
           </Popconfirm>
          </span>
          
        ),

      }
    ];

    const { loading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;
    return (
      <div>
        <div style={{ marginBottom: 16 }}>
        <Popconfirm title="Sure, you want to delete?" onConfirm={() => this.handleDelete()}> 
          <Button type="primary"  disabled={!hasSelected} loading={loading}>
            Delete
          </Button>
        </Popconfirm>
          <Button type="primary" onClick={this.showModal} style={{float:"right"}}>
           Create
          </Button>
          <span style={{ marginLeft: 8 }}>
            {hasSelected ? `Selected ${selectedRowKeys.length} items` : ''}
          </span>
        </div>
        <Table bordered rowSelection={rowSelection} columns={columns} dataSource={this.state.branch} scroll={{ x: 'max-content' }} />
        
        <Modal
          title="City Form"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
        <WrappedRegistrationForm isEditProduct={this.state.isEditProduct} citydata={this.state.city} branchdetail={this.state.branchdetail} handleOk={this.handleOk} fetchbranch={this.fetchbranch} />
        </Modal>
      </div>
      
    );
  }
}
 
export default branch;





