import React from 'react';
import api from '../../Global/Api';
import {Form,Input,Radio,Button,Select,Modal,InputNumber} from 'antd';
import TextArea from 'antd/lib/input/TextArea';

  
  class RegistrationForm extends React.Component {
    constructor(props){
      super(props);
      this.initialValue = {
        id:'',
        branch_name:'',
        branch_add : '',
        branch_email1 : '', 
        branch_email2 : '', 
        branch_email3 :'',
        branch_number1 :'',
        branch_number2 : '',
        branch_number3 : '',
        city:'',
        branch_status : '',
      };
      if(this.props.branchdetail){
        this.state = props.branchdetail
      } else {
        this.state = this.initialState;
      }

      this.handleSubmit = this.handleSubmit.bind(this);
    }

    success() {
      Modal.success({
        content: 'Successively Updated',
      });
    }
    
    error() {
      Modal.error({
        title: 'This is an error message',
        content: 'Error Occurred!!',
      });
    }


    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, value) => {
        if (!err) {
          console.log('Received values of form: ', value);
          this.setState({loading:true});
          if(this.props.isEditProduct){
          api.put(`/api/branch/${this.props.branchdetail.id}`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 200){
                  this.success();
                  this.props.fetchbranch();
              }
            })
            .catch(error => {
              this.error();
            });
          }
            else{
              api.post(`/api/branch`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 201){
                  this.success();
                  this.props.fetchbranch();
              }
            })
            .catch(error => {
              this.error();
            });
            }
          this.props.handleOk();
          this.setState(this.initialState);
         }
      });
    };
  

  componentWillReceiveProps(props){
    if(props.branchdetail){
      this.state = props.branchdetail;
    } else {
      this.state = this.initialState;
    }
  }
    
    render() {
      const { getFieldDecorator } = this.props.form;
      /*console.log(this.state.citydetail)*/

      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
      
      const cityvalue = this.props.citydata.map((city) =>
      <option key={city.id} value={city.id}>{city.city_name}</option>
    );


      return (
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Form.Item label="Branch Name">
            {getFieldDecorator('branch_name', { initialValue: this.state.branch_name,
              rules: [
                {
                  required: true,
                  message: 'Please input city pin!'
                },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item label="Branch Address">
          {getFieldDecorator('branch_add', {initialValue: this.state.branch_add,
            rules: [
              {
                required: true,
                message: 'Please input your Address',
              }
            ],
          })(<TextArea />)}
        </Form.Item>
          <Form.Item label="E-mail 1">
          {getFieldDecorator('branch_email1', {initialValue: this.state.branch_email1,
            rules: [
              {
                type: 'email',
                message: 'The input is not valid E-mail!',
              },
              {
                required: true,
                message: 'Please input your E-mail!',
              },
            ],
          })(<Input />)}
        </Form.Item>
        <Form.Item label="E-mail 2">
          {getFieldDecorator('branch_email2', {initialValue: this.state.branch_email2,
            rules: [
              {
                type: 'email',
                message: 'The input is not valid E-mail!',
              },
            ],
          })(<Input />)}
        </Form.Item>
        <Form.Item label="E-mail 3">
          {getFieldDecorator('branch_email3', {initialValue: this.state.branch_email3,
            rules: [
              {
                type: 'email',
                message: 'The input is not valid E-mail!',
              },
            ],
          })(<Input />)}
        </Form.Item>
        <Form.Item label="Branch Contact1">
          {getFieldDecorator('branch_number1', {initialValue: this.state.branch_number1,
            rules: [
              {
                required: true,
                message: 'Please input your Number',
              },
            ],
          })(<InputNumber  style={{width: '100%' }} />)}
        </Form.Item>
        <Form.Item label="Branch Contact1">
          {getFieldDecorator('branch_number2', {initialValue: this.state.branch_number2,
            rules: [
            ],
          })(<InputNumber  style={{width: '100%' }} />)}
        </Form.Item>
        <Form.Item label="Branch Contact3">
          {getFieldDecorator('branch_number3', {initialValue: this.state.branch_number3,
            rules: [
            ],
          })(<InputNumber  style={{width: '100%' }} />)}
        </Form.Item>
          
         <Form.Item label="City" hasFeedback>
          {getFieldDecorator('city', {initialValue: this.state.city,
            rules: [{ required: true, message: 'Please select State!' }],
          })(
            <Select placeholder="Please select State!" >
            {cityvalue}
            </Select>,
          )}
         </Form.Item>

         <Form.Item label="Branch Status" hasFeedback {...formItemLayout}>
            {getFieldDecorator('branch_status', {initialValue: this.state.branch_status,
              rules: [
                {
                  required: true,
                  type: 'boolean',
                },
              ],
            })(
              <Radio.Group name="radiogroup" >
                <Radio value={true}>
                  Active
                </Radio >
                <Radio value={false}>
                  Disable
                </Radio >
              </Radio.Group>
            )}
          </Form.Item>
          

          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
  const WrappedRegistrationForm = Form.create({ name: 'register' })(RegistrationForm);
   
  export default WrappedRegistrationForm;